* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.wawes {
  display: flex;
  justify-content: space-evenly;
}

.songContainer {
  width: 60%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 60vh;
  justify-content: center;
  background-color: gray;

  img {
    width: 20%;
    border-radius: 50%;
    margin: 2rem;
  }
}